.newCollections{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 300px;
    max-width: 100%;
    margin-bottom: 100px;
    gap: 20px;
}
.newCollections h1 {
    color: #171717;
    font-size: 50px;
    font-weight: 600;
}
.newCollections hr {
    width: 200px;
    height: 6px;
    border-radius: 10px;
    background-color: black;
}
.collection{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  margin-top: 50px;
  gap: 30px;
}
@media(max-width:1280px){
 
    .newCollections h1 {
        font-size: 40px;
    }
    .newCollections hr {
        width: 160px;
        height: 4px;
    }
   .collection{
        gap: 20px;
        margin-top: 30px;
    }
}
@media(max-width:1024px){
 
    .newCollections h1 {
        font-size: 30px;
    }
    .newCollections hr {
        width: 120px;
        height: 3px;
    }
   .collection{
        gap: 15px;
        margin-top: 20px;
    }
}
@media(max-width:800px){
    .newCollections{
        gap: 6px;
    }
    .newCollections h1 {
        font-size: 20px;
    }
    .newCollections hr {
        width: 100px;
    }
   .collection{
        gap: 5px;
    }
    
}
@media(max-width:500px){
    .newCollections{
        gap: 6px;
    }
  
   .collection{
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 20px;
    }
}