.breadCrums{
    display: flex;
    align-items: center;
    gap: 8px;
    color: #5e5e5e;
    font-size: 16px;
    margin: 60px 50px;
    text-transform: capitalize;
    font-weight: 600;
}
@media(max-width:1280px){
    .breadCrums{
            margin: 30px 50px;
            font-size: 14px;
    }
}
@media(max-width:1024px){
    .breadCrums{
            margin: 30px 30px;
            font-size: 13px;
    }
}
@media(max-width:800px){
    .breadCrums{
            margin: 30px 10px;
            font-size: 12px;
    }
}
@media(max-width:500px){
    .breadCrums{
        margin: 30px 5px;

            font-size: 10px;
    }
}